<template>
  <v-app>
    <v-toolbar app>
      <v-toolbar-title class="headline text-uppercase">
        <span class="font-weight-light">Split your Credit now</span>
      </v-toolbar-title>
    </v-toolbar>

    <v-content>
      <Login v-show="showLogin" @submitted="submitted"/>
        <v-progress-circular
                v-show="showLoader"
                :size="70"
                :width="7"
                color="purple"
                indeterminate
        ></v-progress-circular>
        <Credit v-show="showCredit" :amount="amount"/>
    </v-content>
  </v-app>
</template>

<script>
import Login from "./components/Login";
import Credit from "./components/Credit";

export default {
  name: 'App',
  components: {
    Login, Credit
  },
  data () {
    return {
        showLogin: true,
        showLoader:false,
        showCredit:false,
        amount: 0,
    }
  },
    methods:{
      submitted: function(creditReq) {
          this.amount = creditReq;
          this.showLogin = false;
          this.showLoader = true;
          let timer = setTimeout(this.logged, 3000);
      },
      logged: function () {
          this.showLoader= false;
          this.showCredit = true;
      }
    }
}
</script>
