<template>
  <v-container>
      <v-layout justify-center>
          <v-flex mb-4>
              <h1 class="display-2 font-weight-bold mb-3">
                  Welcome
              </h1>
              <p class="subheading font-weight-regular">
                  Please login
              </p>
          </v-flex>
      </v-layout>
      <v-layout justify-center>
          <v-form ref="form" v-model="valid" lazy-validation>
              <v-text-field
                      v-model="form.name"
                      :rules="rules.nameRules"
                      label="Name"
                      required
              ></v-text-field>
              <!--<v-text-field-->
                      <!--v-model="form.email"-->
                      <!--:rules="rules.emailRules"-->
                      <!--label="E-mail"-->
                      <!--required-->
              <!--&gt;</v-text-field>-->
              <v-text-field
                      v-model="form.number"
                      :rules="rules.phoneNumber"
                      label="Phone Number"
                      required
                      mask = "phone"
              ></v-text-field>
              <v-text-field
                      v-model="form.amount"
                      :rules="rules.number"
                      label="Amount needed"
                      required
              ></v-text-field>
              <v-checkbox
                      v-model="form.checkbox"
                      :rules="[v => !!v || 'You must agree to continue!']"
                      label="Do you agree?"
                      required
              ></v-checkbox>
              <v-btn
                  :disabled="!valid"
                  @click="submit"
              >
                  submit
              </v-btn>
          </v-form>
      </v-layout>
  </v-container>
</template>

<script>
  export default {
    name: 'Login',
      data() {
        return {
            valid: true,
            form: {
                name: '', email: '', number: '', amount:100,    checkbox: false
            },
            rules: {
                nameRules: [
                    v => !!v || 'Name is required'
                ],
                emailRules: [
                    v => !!v || 'E-mail is required',
                    v => /.+@.+/.test(v) || 'E-mail must be valid'
                ],
                phoneNumber: [
                    v => !!v || 'Phone number is required',
                ],
                number:[
                    v => !!Number(v) || 'Not valid amount',
                ]
            },
        }},
      methods: {
          submit () {
              if (this.$refs.form.validate()) {
               this.$emit('submitted', this.form.amount);
              }
          },
      }

  }
</script>

