<template>
  <v-container grid-list-md>
      <h3 class="display-2 mb-3">
          Please enter contact info
      </h3>
      <h3>Amount needed {{amount}}$</h3>
    <v-layout row wrap>
        <v-flex v-for="(receiver, i) in receivers"
                :key="i">
            <v-layout>
                <v-text-field class="px-3"
                    v-model="receiver.name"
                    label="Name"
                ></v-text-field>
                <v-text-field class="px-3"
                    v-model="receiver.email"
                    :rules="rules.emailRules"
                    label="E-mail"
                    required
                ></v-text-field>
                <v-text-field class="px-3"
                    v-model="receiver.phoneNumber"
                    :rules="rules.phoneNumber"
                    label="Phone Number"
                    required
                    mask = "phone"
                ></v-text-field>
                <v-btn @click="newReciever(receiver)" :color=receiver.class>{{receiver.btnTxt}}</v-btn>
            </v-layout>
        </v-flex>
    </v-layout>
      <div class="text-xs-center">
          <v-dialog v-model="dialog" width="80%">
              <v-btn block color="success" slot="activator">Get Account Number</v-btn>
              <v-card>
                  <v-card-title class="headline grey lighten-2" primary-title>
                      Your Temporary Number Is Here!
                  </v-card-title>
                  <v-card-text>
                      Please remember you have 30 minutes to use this number
                  </v-card-text>
                  <v-card-text class="creditNumber">
                      <span v-for="n in 4" :key="n">
                      <code> {{Math.floor(Math.random()*10000)}} </code>
                      </span>
                  </v-card-text>
                  <v-card-text>Every card will be billed {{(amount / receivers.length).toFixed(2)}}$</v-card-text>
                  <v-divider></v-divider>
                  <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn
                              color="primary"
                              flat
                              @click="dialog = false"
                      >
                          I agree
                      </v-btn>
                  </v-card-actions>
              </v-card>
          </v-dialog>
      </div>
  </v-container>
</template>

<script>
  export default {
    name: 'Login',
    props: ['amount'],
      data: () => ({
          receivers: [{
              name:'',
              phoneNumber:'',
              email:'',
              approved:false,
              btnTxt: 'send',
              class:'info'
          }],
          dialog: false,
          rules: {
              nameRules: [
                  v => !!v || 'Name is required'
              ],
              emailRules: [
                  v => !!v || 'E-mail is required',
                  v => /.+@.+/.test(v) || 'E-mail must be valid'
              ],
              phoneNumber: [
                  v => !!v || 'Phone number is required',
              ]
          },
      }),
      methods: {
          newReciever: function (contact) {
              const newContact = Object.assign({},contact);
              const approval = setTimeout(this.approve, 2000,newContact);
              this.receivers.push(newContact);
              this.receivers[0].name = '';
              this.receivers[0].phoneNumber = '';
              this.receivers[0].email ='';
              this.receivers[0].approved =false;

          },
          approve: function (contact) {
              contact.approved = true;
              contact.btnTxt = 'approved';
              contact.class = 'success'
          },
      },
  }
</script>

<style>
 .creditNumber{
     padding-left: 30%;
 }
</style>
